import './Question.css';

function Question() {
  return (<div>
    <Q prompt="Who was the first president of the US?" answer="George Washington" number="1" />
    <Q prompt="Who is the current US president?" answer="Joe Biden" number="2" />
  </div>
  )
}

const Q = (props) => {
  let { number } = props
  return (
    <div className="question" >
      <h3 className="prompt">{props.prompt}</h3>
      <form id={"question-form " + number}>
        <input id={number} className="answer-boxes" type="text"></input>
        <input type="submit" hidden className="hiddenForPC"></input>
      </form>
      <p id={"answer " + number} hidden> {props.answer}</p>
    </div>
  )
}

export default Question;