import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import Question from './Question';

ReactDOM.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>,
  document.getElementById('root')
);


ReactDOM.render(
  <React.StrictMode>
    <Question />
  </React.StrictMode>,
  document.getElementById('body')
);

function handleSubmit(text) {
  return event => {
    event.preventDefault()
    var correct_answer = document.getElementById("answer " + text).innerHTML
    var attempted_answer = document.getElementById(text).value
    document.getElementById(text).value = ""
    if (toString(attempted_answer) === toString(correct_answer)) {
      document.getElementById("answer " + text).innerHTML = "Correct Answer!"
      document.getElementById("answer " + text).hidden = false
    }
  }
}

var form1 = document.getElementById("question-form 1")
form1.onsubmit = handleSubmit("1")


var form2 = document.getElementById("question-form 2")
form2.onsubmit = handleSubmit("2")


// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
